  .topnav {
        overflow: hidden;
        border-style:solid; 
        border-width: 1px 0 0 0; 
        width: 100%; 
        min-width: fit-content;    
        justify-content: space-evenly;
        text-align: center;
    }
  
  .topnav a {
    float: left;
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    font-size: large;
    text-decoration: underline solid transparent;
    transition: text-decoration 2s ease;
    align-self: center;
  }
  
  .topnav a:hover {
    font-display: bold;
    color: blue;
    text-decoration: underline solid red;
  }
  
  .topnav a.active {
    color: blue;
    text-decoration: underline solid red;
  }
  
  .topnav .icon {
    display: none;
    margin-right: 20px;
  }

  @media screen and (max-width: 850px) {
    .topnav a:not(:first-child) {display: none;}
    .topnav a.icon {
      float: right;
      display: block;
    }
  }
  
  @media screen and (max-width: 850px) {
    .topnav.responsive {position: relative;}
    .topnav.responsive .icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    .topnav.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }