.slider {
 overflow: hidden;
 width: 100vw;
 height: 60vh;
 position: relative;
}
 
.slider .slide {
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 background-size: cover;
 background-position: center;
 animation: slider 18s infinite;
}
 
.slider .slide:nth-child(1) {
   background-image: url('../../public/images/1.jpg');   
   animation-delay: 0s;  
}

.slider .slide:nth-child(2) {
   background-image: url('../../public/images/2.jpg');
   animation-delay: -3s;
}

.slider .slide:nth-child(3) {
   background-image: url('../../public/images/3.jpg');
   animation-delay: -6s;
}

.slider .slide:nth-child(4) {
   background-image: url('../../public/images/4.jpg');
   animation-delay: -9s;
}

.slider .slide:nth-child(5) {
   background-image: url('../../public/images/5.jpg');
   animation-delay: -12s;
}

.slider .slide:nth-child(6) {
   background-image: url('../../public/images/6.jpg');
   animation-delay: -15s;
}


@keyframes slider {
  0%, 13%, 100% {
    opacity:1;
    animation-timing-function: ease;
    z-index: 0;
  }
  20% {
    opacity: 0;
    animation-timing-function: step-end;
    z-index: 0;
  }
  93% {
    opacity: 1;
    animation-timing-function: step-end;
    z-index: -1;
  }
}

