  .leftBottom {
    float: left;
    width: 35%;
    padding: 0 20px;
    margin-left: 5%;
  }
  
  .middleBottom {
    float: left;
    width: 20%;
    text-align: center;
  } 
  
  .rightBottom {
    float: left;
    width: 35%;
    padding: 0 20px;
    margin-left: 5%;
  }

  .footcopyright
  {
    background-color:white;text-align:left;padding:10px 10px 10px 10%;margin-top:7px;
  }
  .footlogo
  {
    margin-top:7px; width: 100vw; overflow: hidden;
  }
  
  .bottomContactcentertext{
    text-align: center;
  }
  .bottomContactpadding{
    padding-top: 40px;
  }
  .bottomContact{
    overflow: hidden;
  }
  
  .introduction {
    overflow: hidden; 
    width: 100%; 
    font-size: large;
    min-width: fit-content;    
    justify-content: space-evenly;
    text-align: left;
  }
  .introduction .content
  {
    padding: 4% 20% 5% 20%;
    width: 100%;
    line-height: 1.5;
  }

  .introduction .logan{
    width: 100%;
    backdrop-filter: contrast(40%);
 }
  
  .introduction .logan .content{
    padding: 0 20% 0 20%;
    width: 100%;
  }
  
  .introduction .logan .content h2{
     font-size:50px;
     color: black
  }
  .introduction .logan .content h1{
     font-size:60px;
     color:blue
  }
  
  .introduction .logan .content h3{
     font-size:24px;
     color:red
  }
  
  .headerImg {
    width: 100%;
    max-width: 3000px;
    height: auto;
  }
  
  @media screen and (max-width: 950px) {
    /* For mobile phones: */
    .middleBottom, .leftBottom, .rightBottom {
      width: 100%;
    }
  
    .leftBottom {
    padding-left: 15%;
    padding-top: 20px;
    }
    .rightBottom {
        padding-left: 15%;
        padding-top: 20px;
    }    
    .middleBottom .logo{
        height: 50%;
        width: 60%;
    }

    .introduction .logan .content{   
        -webkit-backdrop-filter: contrast(40%);
    }
    .introduction .logan .content h1{
        font-size:40px;
    }
    .introduction .logan .content h2{
        font-size:30px;
    }
    .introduction .logan .content h3{
        font-size:22px;
    }
  
  }

  @media screen and (max-width: 640px) {
    /* For mobile phones: */


    .introduction .logan .content h1{
        font-size:30px;
    }
    .introduction .logan .content h2{
        font-size:20px;
    }
    .introduction .logan .content h3{
        font-size:15px;
    }

    .introduction .content
    {
      padding: 4% 10% 5% 10%;
      
    }
    .introduction .logan .content{
    padding: 0 10% 0 10%;
  }
  }


